import { callGet, callPost } from "../axiosInstance";

const isLoggedIn = async () => {
  return await callGet("/welcome/isLoggedIn");
};

const logOut = async () => {
  return await callGet("/welcome/logout");
};

const checkToken = async (token) => {
  return await callGet("/welcome/check_event_by_token/" + token);
};

const get_Status = async () => {
  return await callGet("/welcome/get_Status/" + 0);
};

const isDeleted = async (id, reverseDelete) => {
  return await callGet("/welcome/isDelete/" + id + "/" + reverseDelete);
};

const getActive = async (id) => {
  return await callGet("/welcome/getActive/" + id);
};

const pickWinner = async (spinId) => {
  return await callGet("/welcome/pickWinner/" + spinId);
};

const getList = async (id) => {
  return await callGet("/welcome/getdata1/" + id);
};

const changeStatusData = async (spinN, reverseState) => {
  return await callGet(
    "/welcome/changeSpinStatus/" + spinN + "/" + reverseState
  );
};

const onUploadData = async (body, config) => {
  return await callPost("/welcome/setData/", body, config);
};

export {
  logOut,
  isLoggedIn,
  checkToken,
  get_Status,
  isDeleted,
  getList,
  changeStatusData,
  onUploadData,
  getActive,
  pickWinner,
};
