<template>
  <div class="p-10 bg-slate-100 flex justify-center" v-if="open">
    <table class="w-8/12">
      <thead class="bg-gray-400 border-b-2 border-gray-200">
        <tr>
          <th class="border border-slate-300 py-2">Бэлэгний төрөл</th>
          <th class="border border-slate-300">Бэлэгний тоо</th>
          <th class="border border-slate-300">Зураг</th>
        </tr>
      </thead>

      <tbody class="shadow-md">
        <tr v-for="item in gifts" :key="item">
          <td class="border border-slate-300 p-2 w-[30%]">
            {{ item.gift }}
          </td>
          <td class="border border-slate-300 p-2 w-[30%]">
            {{ item.win }}
          </td>
          <td class="border border-slate-300 p-2 w-[30%]">
            {{ item.image }}
          </td>
        </tr>
      </tbody>
      <div
        class="bg-gray-300 hover:bg-gray-400 text-gray-800 m-2 py-2 px-4 rounded inline-flex items-center"
      >
        <button @click="$emit('close')">Хаах</button>
      </div>
    </table>
  </div>
</template>

<script>
// Api
import { getList } from "../api/main";

export default {
  name: "DetailComp",
  props: {
    open: Boolean,
    spinId: String,
  },
  emits: ["close"],
  data() {
    return {
      gifts: [],
    };
  },
  methods: {
    getListData() {
      getList(this.spinId).then((result) => {
        console.log("Event ugugdul :", result.data);
        this.gifts = result.data;
      });
    },
  },
  watch: {
    spinId() {
      this.getListData();
    },
  },
};
</script>

