<template>
  <div
    @submit.prevent="submit"
    v-show="open"
    id="App"
    class="fixed flex bg-black bg-opacity-60 top-0 right-0 left-0 z-50 w-full md:inset-0 h-modal md:h-full"
  >
    <form
      class="relative p-10 m-auto bg-slate-200 overflow-y-auto max-h-[80%] shadow-md rounded-sm"
    >
      <button
        v-show="cruise.isShowing"
        @click="cruise.isShowing = false"
        type="button"
        class="absolute top-1 left-1 text-gray-400 bg-transparent hover:bg-gray-200 hover:text-gray-900 rounded-lg text-sm p-1.5 inline-flex items-center dark:hover:bg-gray-600 dark:hover:text-white"
      >
        <svg
          xmlns="http://www.w3.org/2000/svg"
          className="h-6 w-6"
          fill="none"
          viewBox="0 0 24 24"
          stroke="currentColor"
          strokeWidth="{2}"
        >
          <path
            strokeLinecap="round"
            strokeLinejoin="round"
            d="M11 15l-3-3m0 0l3-3m-3 3h8M3 12a9 9 0 1118 0 9 9 0 01-18 0z"
          />
        </svg>
        Буцах
      </button>
      <button
        @click="$emit('close')"
        type="button"
        class="absolute top-1 right-1 text-gray-400 bg-transparent hover:bg-gray-200 hover:text-gray-900 rounded-lg text-sm p-1.5 inline-flex items-center dark:hover:bg-gray-600 dark:hover:text-white"
      >
        <svg
          class="w-5 h-5"
          fill="currentColor"
          viewBox="0 0 20 20"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            fill-rule="evenodd"
            d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z"
            clip-rule="evenodd"
          ></path>
        </svg>
      </button>
      <div v-if="cruise.isShowing" class="md:w-[600px] lg:w-[1000px]">
        <div class="flex">
          <div class="grid grid-cols-4 gap-5" v-show="cruise.gift">
            <div>
              <label
                class="block text-gray-700 text-sm font-bold mb-2 ml-2"
                for="username"
              >
                Бэлэгний нэр
              </label>
              <input
                class="appearance-none m-2 bg-gray-100 text-gray-700 border border-gray-300 rounded py-3 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500"
                type="text"
                placeholder="Урамшууллын төрөл"
                v-model="giftName"
              />
            </div>
            <div>
              <label
                class="block text-gray-700 text-sm font-bold mb-2 ml-2"
                for="username"
              >
                Бэлэгний тоо
              </label>
              <input
                class="appearance-none m-2 bg-gray-100 text-gray-700 border border-gray-300 rounded py-3 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500"
                type="number"
                placeholder="Тоо"
                v-model="amount"
              />
            </div>
            <div>
              <label
                class="block text-gray-700 text-sm font-bold mb-2 ml-2"
                for="username"
              >
                <input
                  class="cursor-pointer accent-green-300 focus:accent-green-500 h-4 w-4 mr-1"
                  type="checkbox"
                  v-model="cruise.checked"
                  @click="imageType = 'text'"
                />
                Зураг оруулах
                {{ imageType }}
              </label>

              <input
                class="form-control m-2 px-3 py-1.5 text-base font-normal text-gray-700 bg-white bg-clip-padding border border-solid border-gray-300 rounded transition ease-in-out focus:text-gray-700 focus:bg-white focus:border-blue-600 focus:outline-none"
                type="file"
                name="file"
                id="image-input"
                @change="onFileSelected"
                v-if="cruise.checked"
              />
            </div>
          </div>
          <div v-show="cruise.hooson">
            <label
              class="block text-gray-700 text-sm font-bold mb-2 ml-2"
              for="username"
            >
              Хоосон таарах
            </label>
            <input
              class="appearance-none m-2 bg-gray-100 text-gray-700 border border-gray-300 rounded w-full py-3 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500"
              type="number"
              placeholder="Хоосон таарах"
              v-model="amount"
            />
          </div>
          <!-- add row button -->
          <div class="m-auto ml-6 pt-6">
            <button
              @click="addRow"
              class="bg-gray-300 hover:bg-gray-400 text-gray-800 m-auto py-2 px-4 rounded inline-flex items-center"
            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
                class="h-4 w-5"
                viewBox="0 0 20 20"
                fill="currentColor"
              >
                <path
                  fillRule="evenodd"
                  d="M10 3a1 1 0 011 1v5h5a1 1 0 110 2h-5v5a1 1 0 11-2 0v-5H4a1 1 0 110-2h5V4a1 1 0 011-1z"
                  clipRule="evenodd"
                />
              </svg>
            </button>
          </div>
        </div>
        <button
          class="bg-gray-300 hover:bg-gray-400 text-gray-800 m-2 py-2 px-4 rounded inline-flex items-center"
          @click="
            (cruise.hooson = !cruise.hooson),
              (cruise.gift = !cruise.gift),
              (imageType = 'text'),
              (giftName = 'hooson')
          "
        >
          Хоосон
        </button>

        <div>
          <h1 class="m-3">List</h1>
          <table class="ml-2 w-full">
            <tr v-for="item in add" v-bind:key="item" class="w-12/12">
              <td class="border border-slate-300 p-2.5 bg-slate-100 w-3/12">
                {{ item.giftName }}
              </td>

              <td class="border border-slate-300 p-2.5 bg-slate-100 w-2/12">
                {{ item.amount }}
              </td>
              <td class="border border-slate-300 p-2.5 bg-slate-100 w-2/12">
                {{ item.gifttype }}
              </td>

              <td class="border border-slate-300 p-2.5 bg-slate-100 w-2/12">
                {{ item?.image?.name }}
              </td>

              <!-- cancel -->
              <td class="p-2.5 w-1/12" v-if="editting === item">
                <button @click="editting = null">
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    class="h-5 w-5"
                    viewBox="0 0 20 20"
                    fill="currentColor"
                  >
                    <path
                      fillRule="evenodd"
                      d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z"
                      clipRule="evenodd"
                    />
                  </svg>
                </button>
                <button @click="removeRow(item)">
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    class="h-5 w-5 mx-3"
                    viewBox="0 0 20 20"
                    fill="currentColor"
                  >
                    <path
                      fillRule="evenodd"
                      d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z"
                      clipRule="evenodd"
                    />
                  </svg>
                </button>
              </td>
              <!-- editing -->
              <td class="p-2.5 max-w-5 w-1/12" v-else>
                <button @click="editting = item">
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    class="h-5 w-5 mx-3"
                    viewBox="0 0 20 20"
                    fill="currentColor"
                  >
                    <path
                      fillRule="evenodd"
                      d="M9 2a1 1 0 00-.894.553L7.382 4H4a1 1 0 000 2v10a2 2 0 002 2h8a2 2 0 002-2V6a1 1 0 100-2h-3.382l-.724-1.447A1 1 0 0011 2H9zM7 8a1 1 0 012 0v6a1 1 0 11-2 0V8zm5-1a1 1 0 00-1 1v6a1 1 0 102 0V8a1 1 0 00-1-1z"
                      clipRule="evenodd"
                    />
                  </svg>
                </button>
              </td>
            </tr>
          </table>
        </div>

        <div>
          <button
            class="bg-gray-300 hover:bg-gray-400 text-gray-800 m-2 py-2 px-4 rounded inline-flex items-center"
            @click="onUpload"
          >
            Батлах
          </button>
        </div>
      </div>

      <!--jackPot iin ner avah hseg-->
      <div class="" v-else>
        <input
          class="appearance-none block m-2 bg-gray-100 text-gray-700 border border-gray-300 rounded py-3 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500"
          type="text"
          placeholder="JackPot Үүсгэх"
          v-model="spinId"
        />
        <button
          class="bg-gray-300 hover:bg-gray-400 text-gray-800 m-2 py-2 px-4 rounded inline-flex items-center"
          @click="cruise.isShowing = true"
        >
          Үүсгэх
        </button>
      </div>
    </form>
  </div>
</template>

<script>
// Api
import { onUploadData } from "../api/main";

export default {
  name: "DialogComp",
  props: {
    open: Boolean,
  },
  emits: ["close"],
  data() {
    return {
      editting: null,
      imageType: "text",
      spinId: null,
      giftName: null,
      amount: null,
      gifttype: null,
      image: null,
      add: [],
      cruise: {
        hooson: false,
        gift: true,
        isShowing: false,
        checked: true,
      },
    };
  },
  methods: {
    addRow() {
      this.add.push({
        spinId: this.spinId,
        giftName: this.giftName,
        amount: this.amount,
        gifttype: this.imageType,
        image: this.selectedFile,
      });

      this.giftName = "";
      this.amount = "";
      this.imageType = "text";
      this.selectedFile = "";
      document.getElementById("image-input").value = null;
      console.log(this.add);
    },
    removeRow: function (val) {
      let gg = this.add.filter(function (item) {
        return item !== val;
      });
      this.add = gg;
    },
    onFileSelected(event) {
      this.selectedFile = event.target.files[0];
      this.imageType = "image";
      console.log(this.imageType);
    },
    onUpload() {
      if (this.add.length > 4) {
        const formData = new FormData();
        formData.append("spinId", this.add[0].spinId);
        for (let i = 0; i < this.add.length; i++) {
          formData.append("giftName[]", this.add[i].giftName);
          formData.append("amount[]", this.add[i].amount);
          formData.append("gifttype[]", this.add[i].gifttype);
          formData.append("image_" + i, this.add[i].image);
        }

        console.log("gifts: ", this.add);
        const config = {
          headers: {
            "content-type": "multipart/form-data",
          },
        };

        onUploadData(formData, config).then((res) => {
          console.log(res);
        });
      } else {
        alert("Бэлэгний төрөл 5-аас бага байна");
      }
    },
  },
};
</script>

<style></style>
