<template>
  <div class="flex justify-center p-10">
    <table class="w-10/12">
      <thead class="bg-gray-400 border-b-2 border-gray-200">
        <tr>
          <th class="border border-slate-300 w-[5%]">id</th>
          <th class="border border-slate-300 w-[30%]">Таны JackPot-ууд</th>
          <th class="border border-slate-300 w-[10%]">Нийт бэлэг</th>
          <th class="border border-slate-300 w-[12%]">Token</th>
          <th class="border border-slate-300 w-[25%]">Status</th>

          <!-- <th class="border border-slate-300 w-[8%]"></th>
          <th class="border border-slate-300 w-[5%]">Edit</th> -->
        </tr>
      </thead>

      <tbody class="shadow-md">
        <tr v-for="item in Names" v-bind:key="item.id">
          <td class="border border-slate-300 p-2">
            {{ item.id }}
          </td>
          <td class="border border-slate-300 p-2">
            {{ item.spinname }}
          </td>
          <td class="border border-slate-300 p-2">{{ item.prizeNumber }}</td>
          <td class="border border-slate-300">
            <!-- {{ item.token }} -->
            <button
              @click="copy(item.id)"
              class="bg-gray-300 hover:bg-gray-400 text-gray-800 m-2 py-2 px-4 rounded inline-flex items-center"
            >
              token copy
            </button>
          </td>
          <td class="border border-slate-300">
            <!-- <span
              class="px-4 pb-2 p-1.5 mx-4 text-xs font-medium uppercase tracking-wider text-green-800 bg-green-200 rounded-lg bg-opacity-50"
            >
              {{ item.onProcess }}
            </span> -->

            <Switch
              @click="changeStatus((spinId = item.id), item.onProcess)"
              :class="item.onProcess === '1' ? 'bg-teal-600' : 'bg-red-500'"
              class="mx-4 relative inline-flex flex-shrink-0 h-[25px] w-[45px] border-2 border-transparent rounded-full cursor-pointer transition-colors ease-in-out duration-200 focus:outline-none focus-visible:ring-2 focus-visible:ring-white focus-visible:ring-opacity-75"
            >
              <span
                :class="
                  item.onProcess === '1' ? 'translate-x-5' : 'translate-x-0'
                "
                class="inline-block h-5 w-5 transform rounded-full bg-white"
              />
            </Switch>
            Switch Status
          </td>
          <button
            class="bg-gray-300 hover:bg-gray-400 text-gray-800 m-2 py-2 px-4 rounded inline-flex items-center"
            @click="getListData(item.id)"
          >
            Details
          </button>
          <td class="w-[6%]" v-if="editting === item.id">
            <!-- cancel -->
            <button @click="editting = null">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                class="h-5 w-5"
                viewBox="0 0 20 20"
                fill="currentColor"
              >
                <path
                  fillRule="evenodd"
                  d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z"
                  clipRule="evenodd"
                />
              </svg>
            </button>
            <!-- batlah -->
            <button @click="dell(item.id)">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                class="h-5 w-5 mx-3"
                viewBox="0 0 20 20"
                fill="currentColor"
              >
                <path
                  fillRule="evenodd"
                  d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z"
                  clipRule="evenodd"
                />
              </svg>
            </button>
          </td>
          <!-- delete -->
          <td class="w-[6%]" v-else>
            <button @click="editting = item.id">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                class="h-5 w-5"
                viewBox="0 0 20 20"
                fill="currentColor"
              >
                <path
                  fillRule="evenodd"
                  d="M9 2a1 1 0 00-.894.553L7.382 4H4a1 1 0 000 2v10a2 2 0 002 2h8a2 2 0 002-2V6a1 1 0 100-2h-3.382l-.724-1.447A1 1 0 0011 2H9zM7 8a1 1 0 012 0v6a1 1 0 11-2 0V8zm5-1a1 1 0 00-1 1v6a1 1 0 102 0V8a1 1 0 00-1-1z"
                  clipRule="evenodd"
                />
              </svg>
            </button>
          </td>
        </tr>
      </tbody>

      <button
        class="bg-gray-300 hover:bg-gray-400 text-gray-800 m-2 py-2 px-4 rounded inline-flex items-center"
        @click="$emit('add')"
      >
        <svg
          xmlns="http://www.w3.org/2000/svg"
          class="h-6 w-6"
          fill="none"
          viewBox="0 0 24 24"
          stroke="currentColor"
          strokeWidth="{2}"
        >
          <path
            strokeLinecap="round"
            strokeLinejoin="round"
            d="M4 16v1a3 3 0 003 3h10a3 3 0 003-3v-1m-4-8l-4-4m0 0L8 8m4-4v12"
          />
        </svg>
        <span>Нэмэх</span>
      </button>
    </table>
  </div>
</template>

<script>
// Api
import { get_Status, changeStatusData, isDeleted } from "../api/main";

// Headless UI
import { Switch } from "@headlessui/vue";

export default {
  name: "MainTable",
  components: {
    Switch,
  },
  data() {
    return {
      Names: [],
      editting: null,
      tokenCopy: [],
      deleted: "",
    };
  },
  emits: ["openDetail", "add"],
  methods: {
    getEvent() {
      this.Names = [];
      get_Status().then((result) => {
        this.Names = result.data;
        this.tokenCopy = result.data;
        console.log("niit ugugdul :", result.data);
      });
    },

    copy(id) {
      let pis = this.tokenCopy.filter(function (item) {
        if (item.id === id) {
          return item;
        }
      });
      navigator.clipboard.writeText(pis[0]?.token);
    },

    dell(id) {
      let reverseDelete = 1;
      isDeleted(id, reverseDelete)
        .then((result) => {
          this.getEvent();
          this.status = result.data;
          console.log(id, reverseDelete, "mini");
        })
        .catch((error) => console.log(error));
    },

    changeStatus(spinN, state) {
      this.status = [];
      let reverseState;

      if (Number(state) == 0) {
        reverseState = 1;
      } else {
        reverseState = 0;
      }

      var that = this;
      changeStatusData(spinN, reverseState)
        .then((result) => {
          that.getEvent();
          this.status = result.data;
        })
        .catch((error) => console.log(error));
    },

    getListData(spinId) {
      this.$emit("openDetail", spinId);
    },
  },
  mounted() {
    // db ees irsen uramshuulliin turul bolon hemjeeg delgetsend gargah heseg
    this.getEvent();
  },
};
</script>

<style></style>
