<template>
  <form @submit.prevent="ActiveSpin()">
    <input
      type="text"
      required
      placeholder="Token"
      name="Token"
      autocomplete="off"
      v-model="selectedToken"
    />

    <button type="submit">Нэвтрэх</button>
  </form>
  <!-- {{ $store.state.data }} -->
</template>

<script>
// import Cookies from "js-cookie";
import { checkToken } from "../api/main";

export default {
  name: "App",
  setup() {},
  data() {
    return {
      loggedIn: this.$store.state.isPlayable,
      selectedToken: null,
      id: null,
    };
  },

  watch: {
    loggedIn(newValue) {
      console.log(newValue, "this is new value");
      if (newValue === true) {
        this.$router.push("/SlotMachine/" + this.id);
      }
    },
  },

  methods: {
    ActiveSpin() {
      checkToken(this.selectedToken)
        .then((result) => {
          if (result.data.status == "success") {
            // Cookies.set("token", result.data.token);
            // console.log(this.$store.state.isPlayable);
            this.$store.commit("LogIn");
            this.$router.push("/SlotMachine/" + result.data.id);
            this.id = result.data.id;
          } else {
            alert(result.data.message);
          }
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
  created() {},
};
</script>

<style></style>
